body {
  font-family: var(--basic-font-family);
  background-color: var(--secondary-color);
  font-weight: 400;
}

body.dark-theme {
  color: var(--secondary-color);
}

.fav-container {
  margin: 0 auto;
  padding: 0 16px;
}

.fav-hero-pic img {
  max-height: 516px;
  border-radius: 15px;
  margin-top: 50px;
}

.fav-content-section {
  padding-top: 42px;
  padding-bottom: 42px;
}

.fav-category-recipe-list {
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 24px;
  display: flex;
}

.category-btn {
  color: rgba(5, 5, 5, .3);
  background: none;
  border-color: rgba(5, 5, 5, .2);
  border-radius: 15px;
  padding: 14px 18px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.category-btn:hover, .category-btn:focus, .category-btn:active {
  background-color: var(--btn-color);
  color: var(--secondary-color);
}

.fav-resipes-list {
  flex-wrap: wrap;
  gap: 24px;
  display: flex;
}

.fav-recipes-list-item {
  position: relative;
}

.fav-recipe-card img {
  border-radius: 15px;
}

.recipe-card-gradient {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(5, 5, 5, 0), rgba(5, 5, 5, .6));
  border-radius: 15px;
  position: absolute;
}

.fav-recipe-card-title {
  text-transform: uppercase;
  color: var(--secondary-color);
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  position: absolute;
  bottom: 103px;
  left: 16px;
}

.fav-recipe-card-description {
  width: 303px;
  height: 32px;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: var(--secondary-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 1.33;
  position: absolute;
  bottom: 63px;
  left: 16px;
  overflow: hidden;
}

.fav-recipe-card-button {
  width: 89px;
  height: 31px;
  background-color: var(--btn-color);
  color: var(--secondary-color);
  border: inherit;
  border-radius: 8px;
  padding: 8px 14px;
  font-size: 12px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 16px;
  right: 16px;
}

.fav-recipe-card-button:is(:hover, :focus) {
  color: var(--main-color);
}

.category-btn.is-hidden, .fav-no-recipes-content.is-hidden {
  display: none;
}

.fav-no-recipes-content {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.no-recipes-icon {
  width: 68px;
  height: 58px;
  margin-bottom: 24px;
}

.no-resipes-descr {
  max-width: 335px;
  text-align: center;
  font-size: 14px;
  line-height: 18px;
}

@media screen and (min-width: 375px) {
  .fav-container {
    width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .fav-container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .fav-hero-pic img {
    height: 356px;
  }

  .fav-content-section {
    padding-top: 64px;
    padding-bottom: 319px;
  }

  .fav-category-recipe-list {
    gap: 15px;
    margin-bottom: 32px;
  }

  .category-btn {
    font-size: 16px;
    line-height: 1.25;
  }

  .fav-resipes-list {
    gap: 16px;
  }

  .fav-recipe-card img {
    width: 224px;
    height: 244px;
  }

  .fav-recipe-card-description {
    width: 192px;
    height: 32px;
  }

  .no-recipes-icon {
    width: 97px;
    height: 83px;
    margin-bottom: 32px;
  }

  .no-recipes-descr {
    max-width: 481px;
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (min-width: 1280px) {
  .fav-container {
    width: 1280px;
    padding-left: 100px;
    padding-right: 100px;
  }

  .fav-content-section {
    padding-bottom: 64px;
  }

  .fav-hero-pic img {
    margin-top: 40px;
  }

  .fav-resipes-list {
    gap: 32px 16px;
  }

  .fav-recipe-card img {
    width: 258px;
    height: 287px;
  }

  .fav-recipe-card-description {
    width: 226px;
  }
}

